<template>
  <div>
    <canvas width="600" height="500" ref="test" />
  </div>
</template>
<script>
import circlesJSON from "../assets/circles.json";

import Collasion from "../service/collasion";
export default {
  mounted() {
    this.Collasion = new Collasion(this.$refs.test, circlesJSON);
    this.CollasionAnimation();
  },
  data() {
    return {
      Collasion: null,
    };
  },
  methods: {
    CollasionAnimation() {
      this.Collasion.update();
      this.Collasion.draw();
      requestAnimationFrame(this.CollasionAnimation);
    },
  },
};
</script>