// conecept force = mass* accelration
//          force = (constant * mass1 * mass2 )/ (distance)^2
// fx = fcos0 => f*dx/distance
// fy = fsin0 => f*dy/distance

import Canvas from "./canvas";
export default class Collasion {
    constructor(canvas, data) {
        this.x;
        this.y;
        this.vx = 1;
        this.vy = 1;
        this.circlesData = data.data;
        this.canvas = canvas;
        this.Ccanvas = new Canvas();
        this.canvas = canvas;
        this.context = this.Ccanvas.createCanvas(this.canvas, '2d');
    }
    draw() {
        this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
        this.circlesData.forEach(d=>{
        this.context.fillColor = 'red';
        this.context
        .beginPath()
        .createArc(d.x, d.y, d.radius, 0, 360)
        .fill().closePath().strokeRect(0, 0, this.canvas.width, this.canvas.height);
        })
    }
    update() {
        let dt = 1;
        for (let [i, o1] of this.circlesData.entries()) { // For each pair of objects...
            for (let [j, o2] of this.circlesData.entries()) {
                if (i < j) { // To not do same pair twice
                    let dx = o2.x - o1.x; // Compute distance between centers of objects
                    let dy = o2.y - o1.y;
                    let r = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));
                    if (r <= 1) { // To avoid division by 0
                        r = 1;
                    }
                    if (r < o1.radius + o2.radius) {
                        o1.vx = -o1.vx;
                        o1.vy = -o1.vy;
                        o2.vx = -o2.vx;
                        o2.vy = -o2.vy;
                        // console.log("collasion");
                    }
                    // Compute force for this pair; k = 1000
                    // let f = (0.01 * o1.m * o2.m) / Math.pow(r, 2);
                    // let fx = f * dx / r; // Break it down into components
                    //let fy = f * dy / r;
                    //o1.fx += fx; // Accumulate for first object
                    //o1.fy += fy;
                    //o2.fx -= fx; // And for second object in opposite direction
                    //o2.fy -= fy;
                }
            }
        }
        for (let o1 of this.circlesData) { // for each object update...
            // let ax = o1.fx / o1.m; // ...acceleration 
            //let ay = o1.fy / o1.m;

            if (o1.x > 600 - o1.radius || o1.x < o1.radius) {
                o1.vx = -o1.vx;
            }


            if (o1.y > 500 - o1.radius || o1.y < o1.radius) {
                o1.vy = -o1.vy;
            }

            // console.log(o1.vx);

            //o1.vx += ax * dt; // ...speed
            //o1.vy += ay * dt;


            o1.x += o1.vx * dt; // ...position
            o1.y += o1.vy * dt;
            
            // this.draw(o1);

        }
    }
}